import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import Impacto from '../../componets/newsCatagory/Impacto'
import MasVistos from '../../componets/newsCatagory/MasVistos'
import NewNews from '../../componets/newsCatagory/NewNews'
import SideBar from '../../componets/sideBar/SideBar'




const InicioScreen = () => {
    const imgTop = useSelector(state => state.img.imgUrl[1])
    const imgStar = useSelector(state => state.img.imgUrl[2])
    const imgButton = useSelector(state => state.img.imgUrl[3])



    return (
        <div className='container mb-5'>

            {
                imgTop &&
                    imgTop.estado
                    ? <div className='imgTop'> <img className='img-fluid' src={imgTop.imagen} alt='247NoticiasPanama' /></div>
                    : <span></span>
            }



            <Impacto />
            {
                imgStar &&
                    imgStar.estado
                    ? <div className='imgStar'> <img className='img-fluid' src={imgStar.imagen} alt='247NoticiasPanama' /></div>
                    : <span></span>
            }

            <div className='row'>

                <div className='col-lg-9  col-md-12'>
                    <NewNews />
                </div>

                <div className='col-lg-3  col-md-12 mt-5'>
                    <SideBar />
                </div>
            </div>

            {
                imgButton &&
                    imgButton.estado
                    ? <div className='imgButtom'> <img className='img-fluid' src={imgButton.imagen} alt='247NoticiasPanama' /></div>
                    : <span></span>
            }
            <div>
                <div className='title-header mt-5 mb-4'>
                    <h2 className='title-header-h'>
                        <Link to='/' >LO MS LEDO</Link>
                    </h2>
                </div>


                <div className='row'>

                    <MasVistos />
                </div>

            </div>
        </div>

    )
}

export default InicioScreen