import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import { startChecking } from '../actions/auth'
import { startLoadingImgPublicity } from '../actions/publicity'
import { visitCounter } from '../helpers/visitCounter'
import AdminRouter from './AdminRouter'
import AuthRouter from './AuthRouter'
import DashboardRouter from './DashboardRouter'
import PrivateRouter from './PrivateRouter'
import PublicRouter from './PublicRouter'

const AppRouter = () => {
    const dispatch = useDispatch()

    const { checking, uid } = useSelector(state => state.auth)

    useEffect(() => {
        dispatch(startChecking())
        dispatch(startLoadingImgPublicity())
        visitCounter()
    }, [dispatch])

    if (checking) {
        return (<div className="lds-facebook"><div></div><div></div><div></div></div>)
    }

    return (
        <BrowserRouter>
            <Routes>



                <Route path='/*' element={<DashboardRouter />} />


                <Route path='auth/*' element={< PublicRouter user={!!uid}>
                    <AuthRouter />
                </PublicRouter>} />

                <Route path='admin/*' element={<PrivateRouter user={!!uid}>
                    <AdminRouter />
                </PrivateRouter>} />
            </Routes>
        </BrowserRouter>
    )
}

export default AppRouter