import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { startMostViewed } from '../../actions/newsCategory'
import ViewsMostViewed from '../views/ViewsMostViewed'

const MasVistos = () => {

    const dispatch = useDispatch()

    const { mostViewed } = useSelector(state => state.news)


    useEffect(() => {
        if (!mostViewed) {
            dispatch(startMostViewed('masvistos'))
        }
    }, [dispatch, mostViewed])



    return (

        <ViewsMostViewed
            mostViewed={mostViewed}
        />

    )
}

export default MasVistos