import React from 'react'
import { Link } from 'react-router-dom'

const SidebarAdmin = () => {
    return (

        <div className='col-2 pt-2 sidebar-admin'>
            <Link to='/' className='admin__link' ><i className="bi bi-house-door"></i>Inicio</Link>
            <Link to='news' className='admin__link' ><i className="bi bi-newspaper"></i>Noticias</Link>
            <Link to='publicity' className='admin__link' ><i className="bi bi-card-image"></i>Publicidad</Link>

        </div>

    )
}

export default SidebarAdmin