import React from 'react'
import { useSelector } from 'react-redux'


const Publicity = () => {


    const imgUrl = useSelector(state => state.img.imgUrl[0])


    return (
        <>
            {
                imgUrl &&
                    imgUrl.estado
                    ? <div className='imgEnd mt-1 mb-4'> <img className='img-fluid' src={imgUrl.imagen} alt='247NoticiasPanama' /></div>
                    : <span></span>
            }

        </>
    )
}

export default Publicity