import React, { useState } from 'react'
import { Card, Col, Form, Image, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { startPublicityImg, startPublicityState } from '../../../actions/publicity'

const Publicity = () => {
    const dispatch = useDispatch()
    const { imgUrl } = useSelector(state => state.img)

    const [formError, setFormError] = useState(false)


    const handleFileChange = (e) => {


        const [file] = e.target.files

        console.log(file)
        const SIZE_50MB = 1500000

        if (file.size > SIZE_50MB) {
            return setFormError('Imagen muy grande, mximo 1.5MB    ')
        } else {
            setFormError(false)
        }
        const isNameOfOneImageRegEx = /.(jpe?g|gif|png)$/i;
        if (!isNameOfOneImageRegEx.test(file.name)) {
            return setFormError('Solo puede subir Imagenes')
        } else {
            setFormError(false)
        }

        dispatch(startPublicityImg(file, e.target.name))

    }

    const handleCheck = (e) => {

        dispatch(startPublicityState(e.target.name, e.target.checked))


    }


    if (!imgUrl.length > 1) {
        return <h2>CArgando</h2>
    }

    return (
        <div className='publicity-admin p-3'>

            <Form >
                {
                    formError &&
                    <span className='alert-error  text-center'>{formError}</span>
                }
                <Row>
                    {
                        imgUrl.map(img => (
                            <Col lg={5} className='mb-5' key={img._id}>
                                <Card>
                                    <Form.Label>Publicidad {img.nombre}</Form.Label>
                                    <Form.Check
                                        type="switch"
                                        name={img._id}
                                        label='Activar'
                                        checked={img.estado}
                                        onChange={handleCheck}
                                    />
                                    <Form.Control

                                        name={img._id}
                                        type="file"
                                        size='sm'
                                        accept='.jpg, .jpeg, .png, gif'
                                        onChange={handleFileChange}

                                    />
                                    <Image src={img.imagen} className="img-thumbnail" alt="..." />
                                    {/* {
                                        imgBase64
                                        ? <Image src={img.imagen} className="img-thumbnail" alt="..." />
                                            : <Image src='' className="img-thumbnail" alt="..." />
                                    } */}
                                </Card>
                            </Col>
                        ))
                    }

                </Row>
            </Form >
        </ div>
    )
}

export default Publicity