import React, { useState } from 'react'
import { Button, Card, Col, Form, Row } from 'react-bootstrap'
import emailjs from '@emailjs/browser'
import validator from 'validator'

import Swal from 'sweetalert2'



const ContactanosScreen = () => {

    const [msgError, setMsgError] = useState(false)

    const handleSubmit = async (e) => {
        e.preventDefault()



        if (e.target.from_name.value.trim().length === 0) {
            setMsgError('El nombre es requerido')
            return
        }
        if (!validator.isEmail(e.target.from_email.value)) {
            setMsgError('El correo no es valido')
            return
        }
        if (e.target.from_subject.value.trim().length === 0) {
            setMsgError('El asunto es requerido')
            return
        }
        if (e.target.message.value.trim().length === 0) {
            setMsgError('El Mensaje es requerido')
            return
        }





        const resp = await emailjs.sendForm('service_dqtnw36', 'template_pm5xezj', e.target, 'edn2Cal5vIAxb3bLP')
        const { text } = resp
        console.log(text)
        if (text) {
            setMsgError(false)

            Swal.fire('Mensaje Enviado')
            e.target.from_email.value = ''
            e.target.message.value = ''
            e.target.from_subject.value = ''
            e.target.from_name.value = ''
        }
    }


    return (
        <Row className='container contactanos mt-5 mb-5  d-md-flex  justify-content-md-around '>
            <div className='text-center mb-3'>

                <h2>QUINES SOMOS?</h2>
            </div>
            <div className='mb-5 info-contactanos  mt-2 '>
                <div> <strong>24/7 Noticias Panam</strong> es el medio digital alternativo lder de Comunicacin, Marketing y Periodismo.</div>

                <div>

                    Est pensado y diseado para ofrecer la actualidad de los Medios y de las empresas de Comunicacin.
                </div>
                <div>
                    Publicamos informacin  diaria y de calidad sobre todos los temas nacionales e internacionales.
                </div>
                <div>
                    Entrevistas, reportajes, exclusivas, vdeos, audios, redes sociales: utilizamos todas las frmulas posibles para hacer llegar nuestra informacin a nuestros lectores.

                </div>
                <div>
                    Para ello contamos con secciones especializadas en Prensa, Radio, Televisin, Periodismo, Comunicacin, Marketing, Salud e Internet y Redes Sociales.

                </div>


            </div>


            <Col md={12} className='text-center mb-5 mt-2'>
                <h2>CONTACTANOS</h2>

                <div className='mb-4 mt-5 text-center d-md-flex  justify-content-md-around '>
                    <Card className="shadow p-2 mb-2 bg-white rounded" style={{ width: '16rem' }}>
                        <Card.Body>
                            <Card.Title><i className="bi bi-phone-vibrate display-6"></i> </Card.Title>
                            <Card.Text>
                                +507 66712059
                            </Card.Text>
                        </Card.Body>
                    </Card>


                    <Card className="shadow p-2 mb-2 bg-white rounded " style={{ width: '18rem' }}>
                        <Card.Body>
                            <Card.Title><i className="bi bi-envelope display-6"></i> </Card.Title>
                            <Card.Text>
                                247noticiaspanama@gmail.com
                            </Card.Text>
                        </Card.Body>
                    </Card>


                    <Card className="shadow p-2 mb-2 bg-white rounded whatsapp-form" style={{ width: '16rem' }}>
                        <a href='https://api.whatsapp.com/message/XWXC6E7J72ZKN1'>
                            <Card.Body>
                                <Card.Title><i className="bi bi-whatsapp display-6"></i></Card.Title>
                                <Card.Text>
                                    +507 66712059
                                </Card.Text>
                            </Card.Body>
                        </a>
                    </Card>

                </div>
            </Col>
            <Col md={11} >


                <Form className="shadow p-4 mb-3 bg-white rounded" onSubmit={handleSubmit}>


                    {msgError &&

                        <div className='alert-error'>{msgError}</div>
                    }

                    <Form.Group as={Row} className="mb-3" controlId="exampleForm.ControlInput1">

                        <Col>
                            <Form.Control type="text" name='from_name' placeholder="Nombre" />
                        </Col>
                        <Col>
                            <Form.Control type="email" name='from_email' placeholder="Correo electronico" />
                        </Col>

                    </Form.Group>

                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                        <Form.Control type="text" name='from_subject' placeholder="Asunto" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">

                        <Form.Control as="textarea" name='message' rows={4} placeholder="Mensaje" />
                    </Form.Group>
                    <Button variant="outline-primary" type="submit">
                        Enviar
                    </Button>
                </Form>
            </Col>


        </Row>
    )
}

export default ContactanosScreen