import React from 'react'
import { Link } from 'react-router-dom'
import MasVistos from '../newsCatagory/MasVistos'
import Publicity from '../publicity/Publicity'
import SocialMediaFollowers from '../socialMedia/SocialMediaFollowers'

const SideBarNews = () => {
    return (
        <div>
            <div className='col-lg-12  col-md-6 mt-3'>

                <Publicity />
            </div>


            <div className='mt-5'>
                <SocialMediaFollowers />
            </div>
            <div>
                <div className='title-header mt-5 mb-3'>
                    <h2 className='title-header-h'>
                        <Link to='/' >LO MS LEDO</Link>
                    </h2>
                </div>
                <MasVistos />
            </div>
        </div>
    )
}

export default SideBarNews