import { types } from "../types/types"


const initialState = {
    imgUrl: []
}


export const publicityReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.startPublicityLoad:
            return {
                ...state,
                imgUrl: [
                    ...action.payload
                ]
            }

        case types.startPublicityUploadImg:
            return {
                ...state,
                imgUrl: state.imgUrl.map(
                    img => img._id === action.payload.id
                        ? action.payload.url
                        : img
                )
            }
        default:
            return state
    }
}