import { fetchSinToken } from "./fetch"

export const visitCounterNews = async (id) => {

    const local = JSON.parse(localStorage.getItem('visitNews'))

    if (local) {

        const expiryDay = JSON.parse(localStorage.getItem('expiryDay'))
        const currentDate = new Date().getTime()

        if (currentDate > expiryDay) {
            localStorage.removeItem('visitNews')
            localStorage.removeItem('expiryDay')
            localStorage.removeItem('visitPage')
            return
        }


        const views = local.find(idL => idL === id)
        if (!views) {
            local.push(id)

            localStorage.setItem('visitNews', JSON.stringify(local))
            return await fetchSinToken(`vistas/${id}`, {}, 'PUT')
        }
        return
    }

    if (!local) {

        const data = [
            id
        ]
        const expiryDay = new Date().setDate(new Date().getDate() + 1)

        localStorage.setItem('expiryDay', JSON.stringify(expiryDay))
        localStorage.setItem('visitNews', JSON.stringify(data))

        return await fetchSinToken(`vistas/${id}`, {}, 'PUT')

    }

}