import React from 'react'
import { Route, Routes } from 'react-router-dom'
import LayoutAdmin from '../componets/layout/LayoutAdmin'
import ViewsNacional from '../componets/publication/ViewsNacional'
import AdminScreen from '../pages/admin/AdminScreen'
import News from '../pages/admin/components/News'
import Publicity from '../pages/admin/components/Publicity'

const AdminRouter = () => {
    return (
        <div>
            <LayoutAdmin>

                <Routes>

                    <Route path="/" element={<AdminScreen />} />
                    <Route path="news/:url" element={<ViewsNacional />} />
                    <Route path="/news" element={<News />} />
                    <Route path="/publicity" element={<Publicity />} />
                </Routes>
            </LayoutAdmin>
        </div>
    )
}

export default AdminRouter