import { fetchConToken, fetchSinToken } from "../helpers/fetch"
import uploadImage from "../helpers/fileCloudinary"
import { types } from "../types/types"




export const startLoadingImgPublicity = () => {
    return async (dispatch) => {
        const resp = await fetchSinToken('publicity')
        const body = await resp.json()
        if (body.ok) {
            dispatch(loadPublicity(body.publicidad))
        }
    }

}

const loadPublicity = (img) => ({
    type: types.startPublicityLoad,
    payload: img
})


export const startPublicityImg = (file, id) => {
    return async (dispatch) => {

        const url = await uploadImage(file)

        if (url) {
            const data = {
                imagen: url
            }
            const resp = await fetchConToken(`publicity/${id}`, data, 'PUT')
            const body = await resp.json()

            dispatch(addPublityEnd(body.publicidad, id))
        }
    }
}


export const startPublicityState = (id, estado) => {
    return async (dispatch) => {

        const data = {
            estado
        }
        const resp = await fetchConToken(`publicity/${id}`, data, 'PUT')
        const body = await resp.json()

        dispatch(addPublityEnd(body.publicidad, id))

    }
}

const addPublityEnd = (url, id) => ({
    type: types.startPublicityUploadImg,
    payload: { url, id }
})
