import { fetchSinToken } from "./fetch"

export const visitCounter = async () => {

    const resp = localStorage.getItem('visitPage')

    if (resp) {

        const currentHours = new Date().getTime()

        if (currentHours > resp) {
            localStorage.removeItem('visitPage')
            const experyDayVisit = new Date().setHours(new Date().getHours() + 1)
            localStorage.setItem('visitPage', experyDayVisit)
            return await fetchSinToken('counter', {}, 'PUT')
        }

    }

    if (!resp) {

        const experyDayVisit = new Date().setHours(new Date().getHours() + 1)
        localStorage.setItem('visitPage', experyDayVisit)

        await fetchSinToken('counter', {}, 'PUT')
        return
    }

}