import { types } from "../types/types"

const initialState = {
    impacto: [],
    newNews: null,
    categoryNews: null,
    activeNews: null,
    nacionales: [],
    internacionales: [],
    policivas: [],
    deportes: [],
    farandula: [],
    cienciaytecnologia: [],
    mostViewed: null


}
export const newsCategoryReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.startNewsImpacto:
            return {
                ...state,
                activeNews: null,
                impacto: [
                    action.payload.firstNews,
                    action.payload.restNews
                ]
            }
        case types.startNewNews:
            return {
                ...state,
                activeNews: null,
                newNews: [
                    ...action.payload
                ]
            }

        case types.startActiveCategoryNews:
            return {
                ...state,
                activeNews: action.payload
            }

        case types.startCategoryNews:
            return {
                ...state,
                activeNews: null,
                total: action.payload.total,
                categoryNews: [
                    ...action.payload.news
                ]
            }

        case '6255c75c9335f16f54bae5cc':
            return {
                ...state,
                activeNews: null,
                nacionales: [
                    action.payload.total,
                    action.payload.news
                ]
            }
        case '6255e5c77deb4b9baae040bd':
            return {
                ...state,
                activeNews: null,
                internacionales: [
                    action.payload.total,
                    action.payload.news
                ]
            }
        case '629fc32a9febcfb2fa0ee6a2':
            return {
                ...state,
                activeNews: null,
                policivas: [
                    action.payload.total,
                    action.payload.news
                ]
            }
        case '6255e5d07deb4b9baae040c1':
            return {
                ...state,
                activeNews: null,
                deportes: [
                    action.payload.total,
                    action.payload.news
                ]
            }
        case '629fc2a73334d42397fb9efe':
            return {
                ...state,
                activeNews: null,
                farandula: [
                    action.payload.total,
                    action.payload.news
                ]
            }
        case '6255f7e7f71ccf5bb78d8c44':
            return {
                ...state,
                activeNews: null,
                cienciaytecnologia: [
                    action.payload.total,
                    action.payload.news
                ]
            }

        case types.startActiveClearCetegoryNews:
            return {
                ...state,
                activeNews: null
            }

        case types.startMostViewed:
            return {
                ...state,
                mostViewed: action.payload
            }

        default:
            return state
    }
}