import React from 'react'
import { Link } from 'react-router-dom'




const ViewsMostViewed = (props) => {


    const { mostViewed } = props

    if (!mostViewed) {
        return <h2>cargando</h2>
    }

    return (
        <>
            {
                mostViewed.map(news => (
                    <div className='most-viewd d-flex mb-4' key={news._id}>
                        <div className=''>
                            <div className='most-viewed-img'>
                                <Link to=''>
                                    <img src={news.url} alt={news.titulo} />
                                </Link>
                            </div>
                        </div>

                        <div className='most-viewed-title'>
                            <h2>
                                <Link to={`/${news.categoria.nombre}/${news.urlNoticia}`}>{news.titulo}</Link>
                            </h2>
                            <Link to={`/${news.categoria.nombre}/${news.urlNoticia}`} className='category' >{news.categoria.nombre}</Link>
                        </div>
                    </div>
                ))
            }
        </>

    )
}

export default ViewsMostViewed